.fixed{
    position: fixed;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 18%;
    height: 90%;
    background-color: #31484B;

    @media only screen and (max-width: 600px) {
        width: 100%;
        height: 5rem;
        flex-direction: row;
        justify-content: center;;
        top: 0%;
      }

    @media only screen and (max-width: 350px){
        font-size: 4rem;
        height: 4.5rem;
    }
}

.active { 
    background-color: #263739 ;
}  

.side-nav{
    font-size: 1.4rem;
    list-style: none;
    margin-top: 3.5rem;
    font-weight: 100;

    @media only screen and (max-width: 600px){
        display: flex;
        margin: 0;
        z-index: 99;
        font-size: 1rem;
    }

    @media only screen and (max-width: 350px){
        font-size: .9rem;
    }

    &__item{
        position: relative;
        &:not(:last-child){
            margin-top: .5rem;

            
            @media only screen and (max-width: 600px){
                margin: 0;
            }
        }


    }

    &__item::before{
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 3px;
        background-color: #263739;
        transform: scaleY(0);
        transition: transform .2s,
         width .4s cubic-bezier(1,0,0,1) .2s,
         background-color .1s;
    }

    &__item:hover::before,  &__item--active::before{
        transform: scaleY(1);
        width: 100%;
    }

    &__link{
        text-decoration: none;
        text-transform: uppercase;
        display: block;
        padding: 1.5rem 3rem;
        position: relative;
        z-index: 10;
        font-weight: 100;
        display:flex;
        align-items: center;

    
        @media only screen and (max-width: 425px){
            padding: 1.5rem 1.4rem;
        }
    }
}

