////////////////////////////
//RESUME
.resume-background{
    min-height: 72rem;
    max-width: 100%;
    display: flex;
    flex-direction: column;
}

.logo2{
    height: 140%;
    padding-left: 3rem;
    padding-top: 3rem;

    @media only screen and (max-width: 600px) {
        padding-left: 1rem;
        padding-top: 1rem;
        height: 11rem;
    }
}

.resume-button{
    padding-right: 4rem;
    padding-top: 3rem;

    @media only screen and (max-width: 420px) {
        padding-right: 1rem;
    }
}

.resume-header{
    display: flex;
    justify-content: space-between;
    height: 20%;
    align-content: space-between;
    width: 100%;
}

.professional{
    height: 55rem;
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    
}

.resume-title{
    text-align: center;
    font-size: 2rem;
}

.title-lower{
    font-size: 1.7rem;
    color: #31484B;
}

/////////////////////////////
//SKILL BOX
.skill-box{
    height: 20rem;
    width: 80%;
    margin: auto;
    border-top: 1px solid #333;
    border-bottom: 1px solid #333;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    @media only screen and (max-width: 575px) {
        width: 100%;
    }
}

.skill-inner-box{
    width: 100%;
    flex: 0 0 30%;
    display: flex;
}

.skill-lang{
    flex: 0 0 15%;
    margin: auto;
    font-size: 1.5rem;
    text-align: center;

    
    @media only screen and (max-width: 475px) {
        font-size: 1rem;
    }
}

.skill{
    flex: 0 0 85%;
    height: 40%;
    margin: auto;
}

.python-percent{
    height: 100%;
    width: 85%;
    background-color: #D9EDF0;
    text-align: right;
    padding-top:.4rem;
    text-align: right;

    transition: width 1s;
    will-change: opacity;
    opacity: 1;
}


.hidden {
    height: 100%;
    width: 0%;
    background-color: #D9EDF0;
    text-align: right;
    padding-top:.4rem;
    text-align: right;

    transition: opacity 500ms ease-out;
    will-change: opacity;
    opacity: 1;
  }
  

.javascript-percent{
    height: 100%;
    width: 90%;
    background-color: #D9EDF0;
    padding-top:.4rem;
    text-align: right;

    transition: width 1s;
    will-change: opacity;
    opacity: 1;
}

.csharp-percent{
    height: 100%;
    width: 60%;
    background-color: #D9EDF0;
    padding-top:.4rem;
    text-align: right;

    transition: width 1s;
    will-change: opacity;
    opacity: 1;
}

//////////////////////////////////
//SKILLS EXTRA

.skills-extra{
    height: 15rem;
    margin-top: 2rem;
    display: flex;
    font-size: 1.3rem;
    width: 80%;
    left: 10%;

}

.skills-list1{
    display: table;
    margin: 0 auto;
    flex: 0 0 50%;
    margin-left:10px;
}

.skills-list2{
    display: table;
    margin: 0 auto;
    flex: 0 0 50%;
    margin-left:10px;
}

.skills-list3{
    display: table;
    margin: 0 auto;
}

////////////////////////////////////
//EXPERIENCE

.experience{
    height: 80rem;
    background-color: white;
    position: relative;
    overflow: hidden;

        
    @media only screen and (max-width: 980px) {
        height: 90rem;
      }

      @media only screen and (max-width: 700px) {
        height: 95rem;
      }
}

.experience-section::before{
    background: url('../assets/background.jpg');
    background-repeat: no-repeat;
    background-size: 100% 100%;
    height: 100%;
    width: 100%;
    content: "";
    position: absolute;
    opacity: .1;
}
.experience-section{
    position: relative; 
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
}

.experience-opaque{
    opacity: 1;
}
.experience-box{
    display: flex;
    width: 100%;
    height: 90%;
    padding-top: 2rem;
}

.experience-left{
    flex: 0 0 50%;
    border-right: 2.5px solid #D9EDF0;
    height: 100%;
    text-align: right;
}

.experience-1{
    float: right;
    width: 80%;

    transition: left 1s;
    opacity: 1;

    @media only screen and (max-width: 500px) {
        width: 100%;
      }
}

.experience-3{
    margin-top: 18rem;
    float: right;
    width: 80%;
    
    @media only screen and (max-width: 500px) {
        width: 100%;
       
      }

    @media only screen and (max-width: 400px) {
        width: 100%; 
        margin-top: 10rem;
    }

}

.experience-2{
    margin-top: 18rem;
    float: left;
    width: 80%;
    
    @media only screen and (max-width: 500px) {
        width: 100%;
       
      }

    @media only screen and (max-width: 400px) {
        width: 100%; 
        margin-top: 10rem;
    }
}

.experience-4{
    margin-top: 18rem;
    float: left;
    width: 80%;
    
    @media only screen and (max-width: 500px) {
        width: 100%;
       
      }

    @media only screen and (max-width: 400px) {
        width: 100%; 
        margin-top: 10rem;
    }
}

.experience-right{
    flex: 0 0 50%;
    border-left: 2.5px solid #D9EDF0;
    height: 100%;
    text-align: left;
}

////////////////////////////
//EDUCATION
.education{
    height: 48rem;
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    padding-top: 1rem;

            
    @media only screen and (max-width: 800px) {
        height: 60rem;
    }
}

.education-title{
    width: 80%;
    border-top: 1px solid #333;
    border-bottom: 1px solid #333;
}

.education-box{
    background-color: #D9EDF0;
    width: 68%;
    height: 42%;
    display: flex;
  
   @media only screen and (max-width: 475px) {
        width: 90%;
      }
}

.school-box{
    flex: 0 0 30%;
    background-color: white;
    height: 90%;
    width: 90%;
    margin-top: 1rem;
    margin-left: 1rem;
    margin-right: 1rem;
    display: flex;
}

.school-box2{
    text-align: right;
}


.indent-education{
    height: 22%;
    margin-top: 2.2rem;
    flex: 0 0 2%;
    background-color: #31484B;
}


.education-title2{
    margin-top: 1.5rem;
    margin-bottom: 2rem;
    margin-left: 2%;
    margin-right: 2%;
    font-size: 1.4rem;
    text-transform: uppercase;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

.title2-lower{
    color: #31484B;
    text-transform: initial;
    font-size: 1.2rem;
    margin-right: 1rem;
}

.school-about{
    flex: 0 0 60;
    padding-left: 1rem;
    font-size: 1.1rem;

    margin-top: 1rem;
    border-left: 1px solid #333;
    margin-left: 1rem;
    height: 90%;
    width: 100%;
}


.school-about2{
    border-right: 1px solid #333;
    margin-right: 1rem;
    border-left: none;
    text-align: right;
    padding-right: 1rem;
}

.school-list{
    list-style: none;
}

.course-list{
    font-size: 1.2rem;
    list-style: none;
    padding-top: 5%;
}

.school-bold{
    font-size: 1.3rem;
}


//////////////////////
//PROJECTS
.projects{
    padding-top: 2rem;
    height: 15rem;
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
  
}

.projects-title{
    border-top: 1px solid #333;
    width: 80%;
}

.projects-button{
    margin: auto;
}

