.background-project{
    background-color: white;
    width: 40rem;
    height: 20rem;
    display: flex;
    justify-content: space-between;
    perspective: 1000px;

    @media only screen and (max-width: 900px) {
      margin: 10px;
    }

    @media only screen and (max-width: 420px) {
      height: 18rem;
      width: 30rem;
  }
}

.project-desc{
    position: absolute;
    top: 20%;
    margin-left: 1rem;
    text-align: left;
    flex-direction: column;
    justify-content: space-around;
    width: 100%;
    text-overflow: wrap;
    flex: 0 0 40%;
}

.indent{
    height:30%;
    margin-top: 3.2rem;
    flex: 0 0 2%;
    background-color: #31484B;
}

.description{
    flex: 0 0 98%;
    
}

.title{
    font-size: 2rem;
    margin-bottom: 0em;
    text-overflow: wrap;

    @media only screen and (max-width: 420px) {
     font-size: 1.8rem;
     width: 6.5rem;
  }
}

.place{
    line-height: 10px;
    display: block;
    margin-top: 0em;
    font-size: 1.2rem;
    color: #31484B;

    @media only screen and (max-width: 420px) {
      width: 6rem;
      margin-top: 1rem;
  }
}

.project-img-box{
    flex: 0 0 60%;
    align-self: right;
}


.proj-img{
    height: 100%;
    width: 100%;
}


.button{
    width: 55%;
    margin-left: 12%;
}

.flip-card {
    background-color: #D9EDF0;
    width: 40rem;
    height: 20rem;
    display: flex;
    justify-content: space-between;
    perspective: 1000px;
    -webkit-perspective: 1000px;
    -o-perspective: 1000px;
    -moz-perspective: 1000px;

    @media only screen and (max-width: 420px) {
      height: 18rem;
      width: 30rem;
  }
  }
  
  .flip-card-inner {
    background-color: white;
    position: relative;
    width: 100%;
    height: 100%;
    text-align: center;
    transition: transform 0.8s;
    transform-style: preserve-3d;
    -webkit-transform-style: preserve-3d;
    -webkit-transition: -webkit-transform 1s;
    -o-transform-style: preserve-3d;
    -o-transition: -o-transform 1s;
    -moz-transform-style: preserve-3d;
    -moz-transition: -moz-transform 1s;

    @media only screen and (max-width: 420px) {
      height: 18rem;
      width: 30rem;
  }
  }
  
  .flip-card:hover .flip-card-inner {
    transform: rotateY(180deg);
    -moz-transform: rotateY(180deg);
    -webkit-transform: rotateY(180deg);
    -o-transform: rotateY(180deg);
  }
  
  
  .flip-card-front, .flip-card-back {
    position: absolute;
    width: 100%;
    height: 100%;
    backface-visibility: hidden;
    -moz-backface-visibility: hidden;
    -webkit-backface-visibility: hidden;
    -o-backface-visibility: hidden;
  }

  .proj-about{
    overflow-wrap: anywhere;
    margin-right: 2rem;
    font-size: 1.2rem;
    display: flex;
    width: 40rem;
    height: 20rem;

    @media only screen and (max-width: 420px) {
      height: 18rem;
      width: 30rem;
  }
}
  
  .flip-card-front {
    width: 40rem;
    height: 20rem;
    display: flex;
    justify-content: space-between;


    @media only screen and (max-width: 420px) {
      height: 18rem;
      width: 30rem;
  }
  }
  
  .flip-card-back {
    background-color: #31484B;
    color: white;
    transform: rotateY(-180deg);
    -moz-transform: rotateY(-180deg);
    -webkit-transform: rotateY(-180deg);
    -o-transform: rotateY(-180deg);

    @media only screen and (max-width: 420px) {
      height: 18rem;
      width: 30rem;
  }
  }

  .list{
      flex: 0 0 20%;
      list-style-type: none;
      border-right: 1px solid white;

    li{
        list-style-type: none;
    }
    
    h1{
        padding-top: 2.5rem;
        font-size: 1.3rem;
        padding-bottom: 1.5rem;
    }
  }

  .proj-about-right{
    flex: 0 0 75%;
    font-size: 1.2rem;
    padding-left: 10px;
    padding-right: 10px;
    text-align: left;
    padding-top: 1rem;

    @media only screen and (max-width: 420px) {
      font-size: .9rem;
      flex: 0 0 80%;
   }
  }