@import "./components/navigation";
@import "./components/button";
@import "./pages/home";
@import "./pages/about";
@import "./pages/projects";
@import "./components/project";
@import "./pages/resume";
@import "./components/button2";
@import "./components/experience";


* {
    margin: 0;
    padding: 0;
}

*,
*::before,
*::after{
    box-sizing: inherit;
}

html{
    box-sizing: border-box;
    font-size: 62.5%; //1rem = 10px
}

body{
    font-family: sans-serif;
    font-weight: 400;
    line-height: 1.6;
    background-color: #31484B;
    background-size: cover;
    min-height: 100%;
    width: 100%;
}

.container{
    display: flex;
    max-width: 120rem;
    height: auto;
    margin: 5rem auto;
    background-color: white;
    position: relative;
    box-shadow: 3px 4px 4px rgba(0, 0, 0, 0.25);

    @media only screen and (max-width: 600px) {
        flex-direction: column;
        align-items: center;
      }
}

.content{
    display: flex;
    flex-direction: column;
    flex: 0 0 82%;
    margin-left: auto;
    width: 100%;
    height: auto;
    min-height: 100vh;
    z-index: 1;
}

.box1{
    height: 1000px;
    width: 100%;
    background-color: pink;

}

.box2{
    height: 1000px;
    width: 100%;
    background-color: blue;
}

.home-page{
    background: white;
    display: flex;
    font-weight: 100;

    
}

.sidebar{
    flex: 0 0 18%;
    color: white;
    font-weight: 100;
    background-color: #31484B;
    position: sticky;
    width: 18%;
    
    @media only screen and (max-width: 600px) {
        width: 100%;
        height: 3%;
        flex-direction: row;
        top: 0%;
        z-index: 2;
      }
}


