.home-page{
    height:auto;
    min-height: 550px;
    display: flex;

    @media only screen and (max-width: 700px) {
        display: block;
      }

    @media only screen and (max-width: 500px){
        flex-direction: column;
        position: relative;
        min-height: 585px;
    }
}

.background-color{
    flex: 0 0 42%;
    position: relative;
    display: flex;

    @media only screen and (max-width: 500px){
        position: absolute;
        width: 100%;
        height: 180px;
        flex-direction: column;
        background-color: #D9EDF0;;
    }
}

.background-rectangle{
    background-color: #D9EDF0;
    position: absolute;
    height: 100%;
    width: 72%;

    @media only screen and (max-width: 500px){
      display: none;
    }
}

.background-home{
    flex: 0 0 58%;
    position: relative;

    @media only screen and (max-width: 500px){
        top: 210px;
        width:100%;
        position: absolute;
        height:380px;
    }
}

.main-photo{
    position: absolute;
    width: 78%;
    top: 6.5rem;
    left: 10rem;

    @media only screen and (max-width: 500px){
        width:200px;
        height:200px;
        object-fit:cover;
        border-radius: 50%;
    }
}

.logo-main{
    position: absolute;
    top: 0%;
    right: 0%;
    padding: 1rem;
    width: 12rem;

    @media only screen and (max-width: 400px){
        width:8rem;
    }

    @media only screen and (max-width: 350px){
        display: none;
    }
}

.intro-container{
    position: absolute;
    height: 74%;
    width: 76%;
    right: 10%;
    top: 13%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    @media only screen and (max-width: 700px) {
        right: 2%;
      }
    
      @media only screen and (max-width: 500px){
        width: 96%;
        right: 2%;
        left: 2%;
        top: 0%;
    }

}

.text-hello{
    font-size: 4rem;
}


.text-intro1{
    font-size: 1.7rem;
    font-weight: 100;

    @media only screen and (max-width: 700px) {
        font-size: 1.4rem;
      }
}

.text-intro2{
    font-size: 1.6rem;
    font-weight: 100;
}

.text-pronouns{
    margin-top: .8rem;
    font-size: 1.2rem;
    font-style: italics;
}

.buttons{
    display: flex;
    justify-content: space-between;

    @media only screen and (max-width: 710px) {
        justify-content: space-evenly;
      }

      @media only screen and (max-width: 500px){
        margin-top: 10px;
        margin-bottom: 10px;
    }
}

.socials-bar{
   display: flex;
   justify-content: center;
}

.arrow-down{
    width: 15px;
    height: 15px;
    position: absolute;
    bottom: 1rem;
    left: 8rem;

    @media only screen and (max-width: 710px) {
        left: 46%;
      }

}

.i {
    font-size: 20px;
    animation: bounce 3s ease infinite;
    background-color: white;
}


@keyframes bounce{
    0%, 25%, 55%, 75%, 100%{
        transform: translateY(0)
    }
    45%{
        transform: translateY(-10px);
    }
    65%{
        transform: translateY(-5px);
    }
    85%{
        transform: translateY(-2.5px);
    }
    }
